<template>
    <div class="notice">
        <indexHeader></indexHeader>
        <div class="pdf">
          <!-- <embed src="https://www.castprofit.com/h5/paper.pdf" width="100%" height="800"> -->
		   <iframe :src="pdfurl" frameborder="0" width="100%" height="800"></iframe>
		   <!-- <embed :src="pdfurl" width="100%" height="800"> -->
		   <p class="arrow" style="text-align:center;color:#000;margin: 20px 0 20px 0;">
		  </p>
        </div>
    </div>
</template>
<script>
import indexHeader from "@/view/indexHeader";
export default {
  name: "noticeList",
  components: { indexHeader },
  data() {
    return {
		pdfurl:''
    };
  },
  created: function() {
	this.getPdf();
  },
  methods: {
	getPdf() {
	  this.$http({
	    url: "/api/get_setting?key=white_paper",
	    method: "get"
	  }).then(res => {
	    if (res.data.type == "ok") {
	      console.log(222);
		  this.pdfurl = res.data.message.white_paper
		  console.log(this.pdfurl)
	    }
	  });
	},
  }
};
</script>
<style lang="scss" scoped>
.pdf{
  text-align:center;
  width: 100%;
  padding-bottom: 15px;
  background: #51565b !important;
}
</style>



