<template>
  <div class="account-main wrap fColor1">
    <div class="title bg-part">{{$t('auth.identity')}}</div>
    <div class="main-content bg-part">
      <div v-show="review_status==0">
        <div class="main-input">
          <p>{{$t('auth.name')}}</p>
          <input type="text" :placeholder="$t('auth.pname')" id="name" v-model="name" class="bg-part bd input_color">
          <p>{{$t('auth.idcard')}}</p>
          <input type="text" :placeholder="$t('auth.pidcard')" id="card" v-model="card_id" class="bg-part bd input_color">
          <!-- <div class="flex  center">
            <span>{{$t('auth.name')}}：</span>
            <input type="text" :placeholder="$t('auth.pname')" id="name" v-model="name" class="bg-part bd input_color">
          </div> -->
          <!-- <div class="flex alcenter center mt20">
            <span>{{$t('auth.idcard')}}：</span>
            <input type="text" :placeholder="$t('auth.pidcard')" id="card" v-model="card_id" class="bg-part bd input_color">
          </div> -->
        </div>
        <!-- <div class="mt40 fColor1 ft14 tc">{{$t('auth.upimg')}}</div> -->
        <div class="idimg">
          <div class="idimgLine">
            <div class="idimgItem">
              <p>身份证正面</p>
              <div class="idimgBox">
                <img :src="src1" alt>
                <input type="file" accept="image/*" name="file" @change="file1">
              </div>
            </div>
            <div class="idimgItem">
              <p>身份证反面</p>
              <div class="idimgBox">
                <img :src="src2" alt>
                <input type="file" accept="image/*" name="file" @change="file2">
              </div>
            </div>
          </div>
          <div class="idimgLine">
            <div class="idimgItem">
              <p>手持身份证及声明</p>
              <div class="idimgBox">
                <img :src="src3" alt>
                <input type="file" accept="image/*" name="file" @change="file3">
              </div>
            </div>
            <div class="idimgItem">
              <p></p>
              <div class="idimgBox">
                <img :src="src4" alt>
              </div>
            </div>
          </div>
          
        </div>
        <div class="updata tc">
          <input type="button" :value="$t('auth.submit')" @click="updata" class="curPer">
        </div>
      </div>
      <div v-show="review_status==1" class="reviewSuccessContainer">
        <!-- <div class="tc ft30 au-statue">{{$t('auth.auditing')}}</div> -->
        <div class="reviewAuditingTitle">
          <img src="../../static/imgs/auditing.png" alt="">
          <span>您的实名认证还在审核中，请耐心等待。</span>
        </div>
      </div>
      <div v-show="review_status==2" class="reviewSuccessContainer">
        <!-- <div class="tc ft30 au-statue">{{$t('auth.certified')}}</div> -->
        <div class="reviewSuccessTitle">
          <img src="../../static/imgs/success.png" alt="">
          <span>您的实名认证已通过。</span>
        </div>
        <div class="reviewSuccessMain">
          <div class="reviewSuccessMainLeft">
            <p class="reviewSuccessMainLeftTitle">姓名</p>
            <p class="reviewSuccessMainLeftText">{{idInfo.name}}</p>
            <p class="reviewSuccessMainLeftTitle">身份证号</p>
            <p class="reviewSuccessMainLeftText">{{idInfo.card_id}}</p>
          </div>
          <div class="reviewSuccessMainRight">
            <img src="../../static/imgs/review_success.png" alt="">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import src1 from '../../static/imgs/shenfen1.png';
import src2 from '../../static/imgs/shenfen2.png';
import src3 from '../../static/imgs/shenfen3.png';
import src4 from '../../static/imgs/shenfen4.png';
export default {
  name: "authentication",
  data() {
    return {
      name: "",
      card_id: "",
      // src1: "../../static/imgs/shenfenzhengzhengmian.png",
      // src2: "../../static/imgs/shenfenzhengfanmian.png",
      // src3: "../../static/imgs/shouchishenfenzheng.png",
      // src1:"../../static/imgs/shenfen1.png",
      // src2:"../../static/imgs/shenfen2.png",
      // src3:"../../static/imgs/shenfen3.png",
      src1:src1,
      src2:src2,
      src3:src3,
      src4:src4,
      review_status: 3,
      idInfo:{}
    };
  },
  created() {
    this.token = localStorage.getItem("token");
  },
  methods: {
    file1() {
      var that = this;
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = function(e) {
        // that.src1 = e.target.result;
      };
      var formData = new FormData();
      formData.append("file", event.target.files[0]);
      $.ajax({
        url: "/api/" + "upload",
        type: "post",
        data: formData,
        processData: false,
        contentType: false,
        success: function(msg) {
          if(msg.type == 'ok'){
            that.src1 = msg.message;
          }else{
            layer.msg(msg.message)
          }
          
        }
      });
    },
    file2() {
      var that = this;
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = function(e) {
        // that.src2 = e.target.result;
      };
      var formData = new FormData();
      formData.append("file", event.target.files[0]);
      $.ajax({
        url: "/api/" + "upload",
        type: "post",
        data: formData,
        processData: false,
        contentType: false,
        success: function(msg) {
          if(msg.type == 'ok'){
             that.src2 = msg.message;
          }else{
            layer.msg(msg.message)
          }
         
        }
      });
    },
    file3() {
      var that = this;
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]);
      reader.onload = function(e) {
        // that.src3 = e.target.result;
      };
      var formData = new FormData();
      formData.append("file", event.target.files[0]);
      $.ajax({
        url: "/api/" + "upload",
        type: "post",
        data: formData,
        processData: false,
        contentType: false,
        success: function(msg) {
          if(msg.type == 'ok'){
            that.src3 = msg.message;
          }else{
            layer.msg(msg.message)
          }
          
        }
      });
    },
    updata() {
      var that = this;
      let name = this.$utils.trim(that.name);
      let card_id = this.$utils.trim(that.card_id);
      if (this.name.length == "") {
        layer.tips(this.$t('auth.pname'), "#name");
        return;
      }
      if (this.card_id.length == "") {
        layer.tips(this.$t('auth.pidcard'), "#card");
        return;
      }
      
      if (that.src1 == "" || that.src2 == '' || that.src3 == '') {
        layer.msg(this.$t('auth.pimg'));
        return;
      }
      this.$http({
        url: "/api/" + "user/real_name",
        method: "post",
        data: {
          name: name,
          card_id: card_id,
          front_pic: that.src1,
          reverse_pic: that.src2,
          hand_pic: that.src3
        },
        headers: { Authorization: that.token }
      })
        .then(res => {
          layer.msg(res.data.message);
          if(res.data.type=='ok'){
            setTimeout(function(){
              that.$router.push("/");
            },500);
          }
        })
        .catch(error => {});
    },
    Info() {
      var that = this;
      this.$http({
        url: "/api/" + "user/center",
        method: "get",
        data: {},
        headers: { Authorization: that.token }
      })
        .then(res => {
          console.log(res);
          that.idInfo = res.data.message;
          that.review_status = res.data.message.review_status;
        })
        .catch(error => {});
    }
  },
  mounted() {
    this.Info();
  }
};
</script>
<style scoped lang="scss">
.account-main {
  // background: #273041;
  .title {
    width: 100%;
    line-height: 60px;
    // background: #1a243b;
    border-radius: 4px;
    padding-left: 20px;
    border-bottom: 1px solid rgba(255,255,255,.05);
    font-size: 16px;
    font-weight: 500;
    color: #87A2CD;
  }
  .main-content {
    // background: #1a243b;
    min-height: 700px;
    border-radius: 2px;
    padding: 30px 180px;
    .reviewSuccessContainer {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
      .reviewAuditingTitle {
        width:462px;
        height:54px;
        background:rgba(44,84,215,0.2);
        border:1px solid rgba(44,84,215,1);
        padding: 0 20px;
        display: flex;
        align-items: center;
        margin-bottom: 30px;
        img {
          width: 15px;
          height: 15px;
          margin-right: 25px;
        }
        span {
          font-size:14px;
          color: #FFFFFF;
        }
      }
      .reviewSuccessTitle {
        width:462px;
        height:54px;
        background:rgba(45,193,138,0.2);
        border:1px solid rgba(45,193,138,1);
        padding: 0 20px;
        display: flex;
        align-items: center;
        margin-bottom: 30px;
        img {
          width: 15px;
          height: 15px;
          margin-right: 25px;
        }
        span {
          font-size:14px;
          color: #FFFFFF;
        }
      }
      .reviewSuccessMain {
        width:462px;
        height:230px;
        background:linear-gradient(135deg,rgba(59,98,167,1) 0%,rgba(31,50,142,1) 100%);
        border-radius:4px;
        padding: 40px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .reviewSuccessMainLeft {
          height: 100%;
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          .reviewSuccessMainLeftTitle {
            font-size: 14px;
            font-weight:400;
            color:rgba(255,255,255,0.75);
          }
          .reviewSuccessMainLeftText {
            font-size: 16px;
            font-weight: 500;
            color:rgba(255,255,255,1);
            padding-left: 20px;
          }
        }
        .reviewSuccessMainRight {
          padding: 10px;
          img {
            width: 103px;
            height: 107px;
          }
        }
      }
    }
    .main-input {
      margin: 0 auto;
      text-align: center;
      // padding-top: 100px;
      p {
        color: #A8C3E0;
        text-align: start;
        height: 40px;
        line-height: 40px;
      }
      span {
        width: 100px;
        color: #A8C3E0;
      }
      input {
        // width: 320px;
        width: 100%;
        font-size: 14px;
        min-height: 46px;
        margin-bottom: 20px;
        // border: 1px solid #4e5b85;
        border: none;
        padding: 0 20px;
        color: #5D6B87;
        font-size: 14px;
        border-radius: 4px;
        background-color: #131D33;
      }
    }
    .idimg {
      .idimgLine {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .idimgItem {
          p {
            font-size: 14px;
            color: #A8C3E0;
            height: 40px;
            line-height: 40px;
          }
        }
      }
      .idimgBox {
        width: 290px;
        height: 140px;
        overflow: hidden;
        position: relative;
        background-size: 100% 100%;
        // border: 1px solid #e2e2e2;
        // margin-left: 50px;
        display: flex;
        justify-content: center;
        input {
          position: absolute;
          z-index: 11110;
          opacity: 0;
          width: 100%;
          height: 140px;
          top: 0;
          cursor: pointer;
        }
        img{
          width: 100%;
        }
      }
    }
    .updata {
      input {
        width: 300px;
        height: 45px;
        border-radius: 4px;
        color: #FFFFFF;
        font-size: 14px;
        margin: 0 auto;
        // margin-left: 100px;
        margin-top: 36px;
        background: #4B87FF;
      }
    }
    .au-statue {
      padding-top: 100px;
      font-size: 30px;
    }
  }
}
</style>



