<template>
  <div>
    <div class="n_new_account_rightTop">
      <p class="n_gray1 n_ft14">{{$t('asset.lever_account')}}{{$t('asset.all_assets')}}(USDT)</p>
      <p class="n_white1 n_ft20 n_mt10">{{totle || '0.00' | toFixeds}}</p>
      <!-- <p class="n_gray1 n_ft12 n_mt5">≈{{CNY || '0.00' | toFixeds}} CNY</p> -->
    </div>
    <div class="contentBK">
      <!-- <div class="top">
            <p>{{$t('asset.all_assets')}}：${{totle || '0.00' | toFixeds}}</p>
      </div>-->
      <div class="content_top flex alcenter fColor2 ft12" style="padding:20px 30px 0 30px;">
        <p class="flex1 tc">{{$t('asset.currency')}}</p>
        <p class="flex1 tc">{{$t('asset.canuse')}}</p>
        <p class="flex1 tc">{{$t('asset.frezz')}}</p>
        <!-- <p class="flex1 tc">BTC估值<i></i></p> -->
        <!-- <p class="flex1 tc">锁仓</p> -->
        <p class="flex1 tc">{{$t('asset.conversion')}}(USDT)</p>
        <p class="flex1 tc">{{$t('td.do')}}</p>
      </div>
      <div class="listDataNull" v-show="list.length == 0">
        <img src="@/assets/images/img_nodata.png" alt="">
      </div>
      <ul class="list fColor1">
        <!-- <li v-for="(item,index) in list" :key="index" v-if="item.is_legal == 1 && item.is_lever == 1" @click="go_legalAccount(item.currency)"> -->
        <li class="curPer bdb" v-for="(item,index) in list" :key="index">
          <!-- <li class="curPer bdb" v-for="(item,index) in list" :key="index" v-if="item.is_lever == 1"> -->
          <!-- <p class="legal_name">{{item.currency_name}}</p> -->
          <div class="balance_detail tc">
            <!-- <div class="use_balance flex1">
              <p class="use_balance_num ft12">{{item.currency_name}}</p>
            </div>-->
            <div class="flex1 tc n_flex n_aicenter n_jccenter">
             <svg class="icon n_mr10" aria-hidden="true" v-if="item.currency_name != 'TCB'">
             	<use :xlink:href="'#icon-' + item.currency_name" />
             	<!-- <use xlink:href="#icon-USDT" /> -->
             </svg>
             <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="40" height="40" viewBox="0 0 800 800" v-else style="margin-left: -10px;">
               <image id="logo竖排" x="253" y="250" width="320" height="347" xlink:href="data:img/png;base64,"/>
             </svg>
              <span class="coin_text">{{item.currency_name}}</span>
            </div>
            <div class="flex1">
              <!-- <p class="lock_balance_num ft12" v-if="item.currency_name=='BTC'||item.currency_name=='ETH'">
                           {{item.lever_balance || '0.00' | toFixeds8}}
              </p>-->
              <p class="lock_balance_num ft12">{{item.lever_balance || '0.00' | toFixeds8}}</p>
            </div>
            <div class="convert flex1">
              <!-- <p class="lock_balance_num ft12" v-if="item.currency_name=='BTC'||item.currency_name=='ETH'">
                           {{item.lock_lever_balance || '0.00' | toFixeds8}}
              </p>-->
              <p class="lock_balance_num ft12">{{item.lock_lever_balance || '0.00' | toFixeds8}}</p>
            </div>
            <div class="convert flex1">
              <!-- <p class="lock_balance_num ft12" v-if="item.currency_name=='BTC'||item.currency_name=='ETH'">
                           {{(item.lever_balance*item.cny_price) || '0.00' | toFixeds8}} (USD)
              </p>-->
              <p
                class="lock_balance_num ft12"
              >{{(((item.lever_balance-0) +(item.lock_lever_balance-0))*item.usdt_price) || '0.00' | toFixeds8}}</p>
            </div>
            <div class="convert flex1 ft12 operation">
              <div class="n_flex n_jccenter">
                <div
                  class="n_flex n_aicenter n_pointer n_plr5"
                  @click="$router.push({path:'/transferLever',query:{id:item.currency}})"
                >
                  <img class="n_icon_size1" src="@/assets/images/new_icons/trade1.png" />
                  <span class="n_ft14 n_blue1">{{$t('asset.transfer')}}</span>
                </div>
                <div
                  class="n_flex n_aicenter n_pointer n_plr5"
                  @click="go_legalAccount(item.currency)"
                >
                  <img class="n_icon_size1" src="@/assets/images/new_icons/trade2.png" />
                  <span class="n_ft14 n_blue1">{{$t('asset.record')}}</span>
                </div>
              </div>

              <!-- <span @click="go_legalAccount(item.currency)">{{$t('asset.record')}}</span>
              <span
                @click="$router.push({path:'/transferLever',query:{id:item.currency}})"
              >{{$t('asset.transfer')}}</span>-->
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      totle: "",
      CNY: "",
      list: []
      //   usprice:''
    };
  },
  filters: {
    toFixeds: function(value) {
      value = Number(value);
      return value.toFixed(4);
    },
    toFixeds8: function(value) {
      value = Number(value);
      return value.toFixed(8);
    }
  },
  created() {
    this.token = localStorage.getItem("token") || "";
  },
  mounted() {
    this.legal();
	this.connect();
  },
  methods: {
    //法币账户
    legal() {
      var that = this;
      this.$http({
        url: "/api/" + "wallet/list",
        method: "post",
        data: {},
        headers: { Authorization: that.token }
      })
        .then(res => {
          if (res.data.type == "ok") {
            that.list = res.data.message.lever_wallet.balance;
            //this.totle = res.data.message.lever_wallet.totle;
            //this.CNY = res.data.message.lever_wallet.CNY;
          } else {
            // layer.msg(res.message);
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    go_legalAccount(currency_id) {
      this.$router.push({
        name: "legalAccount",
        query: {
          currency_id: currency_id
        }
      });
    },
	//获取socket
	connect() {
	  let that = this;
	  //that.$socket.emit("login", localStorage.getItem("user_id"));
	   that.$socket.on("daymarket", msg => {
		   //console.log(99999999)
	    if (msg.type == 'daymarket') {
		  let aslid = this.list;
		  let i = 0;
		  let sum =0;
		  //console.log(msg)
		  for(i in aslid){
			  if( aslid[i].currency == msg.currency_id ){
					that.list[i].usdt_price = msg.now_price;

					sum += msg.now_price * (that.list[i].lever_balance*1 + that.list[i].lock_lever_balance*1)
					//console.log(sum)
			  }else{
				  sum += that.list[i].usdt_price * (that.list[i].lever_balance*1 + that.list[i].lock_lever_balance*1)
					// //console.log(sum);

			  }
		  }
			that.totle = sum;
			that.CNY = that.totle * 6.8;
	    }
	   });
	},
  }
};
</script>
<style scoped>
.operation {
  color: #5697f4;
}
.flex1 {
  flex: 1;
}
.top {
  /* background: #1b1e2e; */
  color: #fff;
  padding: 15px 30px;
}
.all_account {
  color: #61688a;
}
.list {
  padding: 0px 30px 15px;
}
.balance_detail {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* color: #61688a; */
}
.legal_name {
  color: #61688a;
  padding: 10px 0;
}
.use_balance_num,
.lock_balance_num {
  padding: 10px 0;
  margin-top: 5px;
}
.list li {
  border-bottom: 1px solid #303b4b;
}
.list li:last-child {
  border-bottom: none;
}
.convert {
  padding: 10px 0;
}
.listDataNull {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 50px 0;
}
.listDataNull img {
  width: 130px;
  height: 130px;
}
.coin_text {
  display: inline-block;
  width: 40px;
  text-align: start;
  font-size: 12px;
}
</style>



